@import url("https://fonts.googleapis.com/css?family=Bitter:400,400i,700");
counting {
  display: block;
  max-width: 600px;
  margin: 0 auto;
  padding-top: 15px;
}

counting header {
  text-align: center;
  margin-bottom: 40px;
}

counting header:before {
  content: '';
  display: block;
  width: 50px;
  height: 50px;
  margin: 0 auto 5px;
  background-image: url("../images/money-icon.png");
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
}

counting header h1 {
  font-size: 2em;
  font-weight: bold;
}

counting header h2 {
  font-size: 1.3em;
}

counting .counting__bills {
  border-bottom: 3px solid #333;
  margin-bottom: 5px;
}

counting .counting__reset {
  margin-top: 5px;
  float: left;
}

counting .counting__sum {
  display: block;
  width: calc(100% - 90px);
  margin-left: auto;
  font-size: 1.8em;
  font-weight: bold;
  text-align: right;
}

counting footer {
  margin-top: 40px;
  padding-top: 5px;
  border-top: 1px solid #ddd;
  color: #888;
}

counting footer small {
  font-size: 1em;
  float: right;
}

bill {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-bottom: 10px;
  text-align: center;
  font-size: 1.5em;
}

bill .bill__button {
  width: 70px;
  height: 40px;
  border: 1px inset rgba(0, 0, 0, 0.1);
  border-radius: 0;
  padding: 0 0 3px 3px;
  outline: none !important;
  background-image: url("../images/efigie.png"), url("../images/paper.jpg") !important;
  background-size: auto 80%, 150%;
  background-repeat: no-repeat, repeat;
  background-position: 60% center;
  background-blend-mode: color-burn, luminosity;
  text-align: left;
  line-height: 55px;
}

bill .bill__button:focus {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

bill .bill__button:active {
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
}

bill .bill__times, bill .bill__equals {
  color: #ccc;
  margin: 0 5px;
}

bill .bill__sum {
  display: inline-block;
  position: relative;
  -webkit-box-flex: 1;
      -ms-flex: 1 1 auto;
          flex: 1 1 auto;
  font-size: 1.2em;
  font-weight: bold;
  text-align: right;
}

@media screen and (min-width: 425px) {
  bill .bill__sum:before {
    content: 'R$';
    position: absolute;
    top: 0;
    left: 0;
  }
}

@media screen and (min-width: 375px) {
  bill .bill__sum:after {
    content: ',00';
  }
}

bill input {
  display: inline-block;
  width: 70px;
  height: 40px;
  outline: none;
  padding-left: 13px;
  line-height: 40px;
  text-align: center;
}

body {
  font-family: 'Bitter', serif;
  font-size: 14px;
  background-color: #fafafa;
}

